import { Box, Stack, Typography } from "@mui/material";
import React, { useState } from "react";
import "./auth.scss";
import Input from "../../components/input";
import { Email, Password, Person } from "@mui/icons-material";
import Button from "../../components/button";
import cx from "classnames";
import { useNavigate } from "react-router-dom";
import useAuth from "../../store/auth";
import Forgot from "./Forgot";
import officeImage from "../../assets/svg/office-docs.svg";
import smartUdyogLogoImage from "../../assets/svg/smart-udyog-logo.svg";

const AuthenticationPage = () => {
  const [tab, setTab] = useState(0);
  const [modalOpen, setModalOpen] = useState(false);
  const [data, setData] = useState({
    fullname: "",
    email: "",
    password: "",
    secret_key: "",
    username: "",
    name: "",
  });

  const { login, register } = useAuth();

  const onChange = (e) => {
    if (!e.target) return;
    setData((prev) => ({ ...prev, [e.target.name]: e.target.value }));
  };

  const handleAuth = () => {
    const { email, password, fullname, secret_key, username } = data;
    if (tab === 0) {
      login({ username, password });
    }
    if (tab === 1) {
      register({ email, password, fullname, secret_key, username });
    }
  };

  // new variables and functions for new design
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const isFormValid = username && password;

  const handleSubmit = (event) => {
    event.preventDefault();
    login({ username, password });
  };

  return (
    <>
      <div className="container">
        <div className="leftHalf">
          <img
            className="mainPageImage"
            src={officeImage}
            alt="office docs image"
          />
        </div>
        <div className="rightHalf">
          <img
            className="smartUdyogLogoImage"
            src={smartUdyogLogoImage}
            alt="smart udyog logo image"
          />
          <form className="login-form" onSubmit={handleSubmit}>
            <label htmlFor="username">Username</label>
            <input
              className="input-field"
              type="text"
              id="username"
              name="username"
              value={username}
              onChange={(e) => setUsername(e.target.value)}
            />
            <label htmlFor="password">Password</label>
            <input
              className="input-field"
              type="password"
              id="password"
              name="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
            />
            <button
              className={`login-btn ${isFormValid ? "enabled" : "disabled"}`}
              type="submit"
              disabled={!isFormValid}
            >
              Login
            </button>
          </form>

          <p className="login-help-text">
            For any issue in logging in, please contact your supervisor.
          </p>

          <div className="link-container">
            <a className="tnc-link" href="">
              Terms and Conditions
            </a>
          </div>
          <div className="link-container">
            <a className="pp-link" href="">
              Privacy Policy
            </a>
          </div>
        </div>
      </div>
    </>
  );
};

export default AuthenticationPage;
